'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Worker } from '@/lib/types';
import { getWorkers } from '@/lib/storage';

export default function WorkersPage() {
  const [workers, setWorkers] = useState<Worker[]>([]);
  const [filteredWorkers, setFilteredWorkers] = useState<Worker[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterRole, setFilterRole] = useState<string>('all');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadWorkers();
  }, []);

  useEffect(() => {
    filterWorkers();
  }, [workers, searchTerm, filterRole]);

  const loadWorkers = () => {
    try {
      const workersData = getWorkers();
      setWorkers(workersData);
    } catch (error) {
      console.error('Error loading workers:', error);
    } finally {
      setLoading(false);
    }
  };

  const filterWorkers = () => {
    let filtered = workers;

    // Filter by search term
    if (searchTerm) {
      filtered = filtered.filter(worker =>
        worker.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        worker.phone.includes(searchTerm)
      );
    }

    // Filter by role
    if (filterRole !== 'all') {
      filtered = filtered.filter(worker => worker.role === filterRole);
    }

    setFilteredWorkers(filtered);
  };

  const getRoleBadgeColor = (role: string) => {
    switch (role) {
      case 'manager':
        return 'bg-purple-100 text-purple-800';
      case 'supervisor':
        return 'bg-blue-100 text-blue-800';
      case 'labour':
        return 'bg-green-100 text-green-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg text-gray-600">Loading workers...</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Workers</h1>
          <p className="text-gray-600 mt-1">Manage your workforce and worker information</p>
        </div>
        <Button asChild>
          <a href="/workers/new">Add New Worker</a>
        </Button>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="pt-6">
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="flex-1">
              <Input
                placeholder="Search by name or phone..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            <div className="sm:w-48">
              <select
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                value={filterRole}
                onChange={(e) => setFilterRole(e.target.value)}
              >
                <option value="all">All Roles</option>
                <option value="labour">Labour</option>
                <option value="supervisor">Supervisor</option>
                <option value="manager">Manager</option>
              </select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Workers List */}
      {filteredWorkers.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredWorkers.map((worker) => (
            <Card key={worker.id} className="hover:shadow-md transition-shadow">
              <CardHeader className="pb-3">
                <div className="flex justify-between items-start">
                  <CardTitle className="text-lg">{worker.name}</CardTitle>
                  <Badge className={getRoleBadgeColor(worker.role)}>
                    {worker.role}
                  </Badge>
                </div>
              </CardHeader>
              <CardContent className="space-y-3">
                <div>
                  <p className="text-sm text-gray-600">Phone</p>
                  <p className="font-medium">{worker.phone}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Wage Rate</p>
                  <p className="font-medium">${worker.wageRate}/hour</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Join Date</p>
                  <p className="font-medium">{new Date(worker.joinDate).toLocaleDateString()}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Status</p>
                  <Badge className={worker.isActive ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}>
                    {worker.isActive ? 'Active' : 'Inactive'}
                  </Badge>
                </div>
                <div className="pt-3 border-t">
                  <Button variant="outline" size="sm" asChild className="w-full">
                    <a href={`/workers/${worker.id}`}>View Details</a>
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : (
        <Card>
          <CardContent className="text-center py-12">
            <p className="text-gray-600 text-lg mb-4">
              {searchTerm || filterRole !== 'all' ? 'No workers found matching your criteria' : 'No workers added yet'}
            </p>
            <Button asChild>
              <a href="/workers/new">Add Your First Worker</a>
            </Button>
          </CardContent>
        </Card>
      )}

      {/* Summary */}
      <Card>
        <CardHeader>
          <CardTitle>Summary</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
            <div>
              <p className="text-2xl font-bold text-gray-900">{workers.length}</p>
              <p className="text-sm text-gray-600">Total Workers</p>
            </div>
            <div>
              <p className="text-2xl font-bold text-green-600">{workers.filter(w => w.isActive).length}</p>
              <p className="text-sm text-gray-600">Active</p>
            </div>
            <div>
              <p className="text-2xl font-bold text-red-600">{workers.filter(w => !w.isActive).length}</p>
              <p className="text-sm text-gray-600">Inactive</p>
            </div>
            <div>
              <p className="text-2xl font-bold text-blue-600">
                ${workers.filter(w => w.isActive).reduce((sum, w) => sum + w.wageRate, 0).toFixed(2)}
              </p>
              <p className="text-sm text-gray-600">Total Hourly Rate</p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
